//全局
import fromInput from '../../components/form-input/form-input.vue'
import linkage from '../../components/linkage/linkage.vue'
import normalPicker from '../../components/normalPicker/noram-picker.vue'
import chooseImage from '../../components/chooseImage/chooseImg.vue'
import givePower from '../../components/givePower/give-power.vue'
//子组件
import radioSex from './identifyChildCpn/radio-sex.vue'
//js
const db = wx.cloud.database()
const user = db.collection('user')
import {write2cloudStroge} from '../../common/js/common.js'
export default {
	components: {
		fromInput,
		radioSex,
		linkage,
		normalPicker,
		chooseImage,
		givePower
	},
	data() {
		return {
			isShowModal: false,
			sex: 1,
			department: '计算机学院',
			major: '计算机科学与技术学院',
			year: 2018,
			studentIdCard: [],
			userName: '',
			studentNumber: 0   
		}
	},
	methods: {
		onbottomModalclosed() {
			this.isShowModal = false
		},
		onSexChanged(upLoad) {
			this.sex = upLoad
		},
		onChooseMajor(department, major) {
			this.department = department
			this.major = major
		},
		onChooseYear(upLoad) {
			this.year = upLoad
		},
		onGetTempFilePaths(upLoad) {
			this.studentIdCard = upLoad
		},
		onGetInputCentent(upLoad) {
			if(upLoad.type === '姓名') {
				this.userName =  upLoad.content
			}else if(upLoad.type === '学号') {
				this.studentNumber = upLoad.content
			}
		},
		async onSuccessLogin(userInfo) {
			console.log(userInfo)
			uni.showLoading({
				title: '提交数据中'
			})
			const fileID = await write2cloudStroge('studentIdCard/', this.studentIdCard)
			this.write2db(fileID[0], userInfo)
			this.returnFromPage()
		},
		onFailLogin() {
			uni.showModal({
				title: '拒绝实名认证'
			})
		},
		write2db(fileId, userInfo) {
			const formInfo = {
				userName: this.userName,
				studentNumber: this.studentNumber,
				sex: this.sex,
				department: this.department,
				major: this.major,
				year: this.year,
			}
			const identigyInfo = {
				...formInfo,
				fileId,
				...userInfo
			}
			return new Promise((resolve, reject) => {
				user.add({
					data: {
						...identigyInfo
					}
				}).then(res => {
					this.$store.commit('login', identigyInfo)
					console.log(identigyInfo)
					uni.hideLoading()
					resolve()
				})
			})
		},
		returnFromPage() {
			uni.navigateBack({
				delta: 1
			})
		},
		onSubmit() {
			wx.getSetting({
				success: (res) => {
					if(res.authSetting['scope.userInfo']) {
						wx.getUserInfo({
							success: async (res) => {
								uni.showLoading({
									title: '提交数据中'
								})
								const fileID = await write2cloudStroge('studentIdCard/', this.studentIdCard)
								this.write2db(fileID[0], res.userInfo)
								this.returnFromPage()
							}
						})
					}else {
						uni.showToast({
							icon: 'none',
							title: '请你授权小程序'
						})
						this.isShowModal = true
					}
				}
			})
		},
	},
	computed: {
		caniSubmit() {
			return this.studentIdCard.length !== 0 && this.userName.length !== 0 && this.studentNumber != 0
		}
	}
}
